.loader-body {
  width: 100%;
  height: 100vh;
  background-color: #ebebe7;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

@-webkit-keyframes rotateFirst {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotateFirst {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes rotateSecond {
  0% {
    -webkit-transform: rotate(730deg);
            transform: rotate(730deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
@keyframes rotateSecond {
  0% {
    -webkit-transform: rotate(730deg);
            transform: rotate(730deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
.loader {
  height: 200px;
  width: 200px;
  box-sizing: border-box;
  border: 20px solid #505362;
  position: relative;
  -webkit-animation: rotateFirst linear 5s infinite;
          animation: rotateFirst linear 5s infinite;
  border-radius: 45%;
}
.loader::before {
  content: "";
  position: absolute;
  width: 160px;
  height: 160px;
  box-sizing: border-box;
  border: 20px solid #505362;
  -webkit-animation: rotateSecond linear 5s infinite;
          animation: rotateSecond linear 5s infinite;
  border-radius: 20%;
}
.loader::after {
  content: "";
  position: absolute;
  width: 50px;
  top: -25px;
  height: 50px;
  box-sizing: border-box;
  border: 20px solid #AC6064;
  -webkit-animation: rotateSecond linear 5s infinite;
          animation: rotateSecond linear 5s infinite;
  border-radius: 50%;
  background-color: #ebebe7;
}

