    border: 0 !important;
    background: #bda27e;
    background-image: none;
  }
  /* Remove IE arrow */
  select::-ms-expand {
    display: none;
  }
  /* Custom Select */
  .select {
    position: relative;
    display: inline;
    width: 20em;
    height: 3em;
    line-height: 3;
    background: #bda27e;
    overflow: hidden;
    font-family: 'Permanent Marker', cursive;
    border-radius: .25em;
    margin-left: 2rem
  }
  .shade select {
    -webkit-flex: 1 1;
