    color: black;
    cursor: pointer;
    font-size: 16px;
    width: 31rem;
  }
  /* Arrow */
  .select::after {
    content: '\25BC';
    position: absolute;
    display:inline;
    top: -11px;
    right: 0;
    padding: 0 1em;
    background: #bda27e;
    cursor: pointer;
    pointer-events: none;
    transition: .25s all ease;
    font-family: 'Permanent Marker', cursive;
  }
  /* Transition */
